import React, { useState } from 'react';
import './Popup.css'; // Import a CSS file for styling

import { companies } from '../../data/companies';

const PopupBox = ({ isOpen, handleClose }) => {
  console.log("here!", isOpen);
  // const [isOpen, setIsOpen] = useState(true);
  const [inputValue, setInputValue] = useState('');
  const [queryResult, setQueryResult] = useState('');
  const [query2Result, setQuery2Result] = useState('');

  // const handleClose = () => {
  //   setIsOpen(false);
  // };

  const handleButtonClick = (url) => {
    window.open(url, '_blank');
  };

  const handleInputChange = (event) => {
    setInputValue(event.target.value);
  };

  const handleQuery = () => {
    const matchingCompanies = companies.filter((companyObject) =>
        companyObject.company.toLowerCase().includes(inputValue.toLowerCase())
    );
    console.log(matchingCompanies);

    const result = matchingCompanies[0].message || 'Not found';
    setQueryResult(result);
    const result2 = matchingCompanies[0].message2 || 'Not found';
    setQuery2Result(result2);
  };

  return (
    <div className={`popup ${isOpen ? 'open' : 'closed'}`}>
      <div className="popup-box">
        <div className="text-close">
            <p className='text'>Please select one of the options or input your company's name below.</p>
            <button className="close-button" onClick={handleClose}><span>&times;</span></button>
        </div>
        { queryResult ?
        <div className="query-result">
            <p className="query-result">{queryResult}</p>
            <p className="query-result">{query2Result}</p>
        </div>
        :
        <div>
        <div className="popup-buttons">
            <button className="resume-button" onClick={() => handleButtonClick('https://drive.google.com/file/d/1Y-8UIE0NS49xCpxrEUlWGsV7txu4kVUn/view?usp=sharing')}>Resume</button>
            <button className="linkedin-button" onClick={() => handleButtonClick('https://www.linkedin.com/in/ashna-khetan/')}>LinkedIn</button>
        </div>
        <div className="input-button">
            <input
            className='input-text'
            type="text"
            placeholder="Enter your company name..."
            value={inputValue}
            onChange={handleInputChange}
            />
            <button className="submit-button" onClick={handleQuery}>Submit</button>
        </div>
        <div className="query-result">{queryResult}</div>
    </div>
        }
      </div>
    </div>
  );
};

export default PopupBox;