import React, { useState, useEffect } from "react";
import { Element } from "react-scroll";

import PageWrapper from "../components/PageWrapper";
import Popup from "../components/Popup";
import Hero from "../sections/landing1/Hero";
import Works from "../sections/landing1/Works";
import Contact from "../sections/landing1/Contact";

const IndexPage = () => {
  const [showPopup, setShowPopup] = useState(true);

  useEffect(() => {
    // Check a condition to determine whether the popup should be shown.
    // For example, you can check if a user has visited the site before.
    const hasVisitedBefore = localStorage.getItem('visitedBefore');

    if (!hasVisitedBefore) {
      // If the user hasn't visited before, set showPopup to true and store that they have visited.
      setShowPopup(true);
      localStorage.setItem('visitedBefore', 'true');
    }
  }, []);

  console.log(showPopup);

  return (
    <>
      <PageWrapper>
        {showPopup && <Popup />}
        <Hero />
        <Element name="works">
          <Works />
        </Element>

        <Contact />
      </PageWrapper>
    </>
  );
};
export default IndexPage;
